import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ExitDetailsPageComponent } from './exit-details-page/exit-details-page.component';
import { RoutePrivilegeGuard } from 'src/app/erp-procurement/data/services/_AccessControlAuthGuard.service';

const routes: Routes = [
  {
    path: "",
    component: ExitDetailsPageComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {
      clientName: "EmployeePortalModule",
      requiredPrivilege: ["Dashboard"],
    },
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class EmployeeExitRoutingModule { }
